<template>
  <div class="d-flex flex-fill flex-column align-self-center login-form">
    <PasswordInput
      :maxlength="8"
      :multiline="false"
      v-model="password"
      root-class="mb-1 login-form__password-input"
    />

    <ActionButton class="mb-4 mt-1" @click="onSubmit">Войти</ActionButton>

    <VTypography
      font-style="text-2"
      font-weight="medium"
      class="text-center login-form__info-text"
    >
      Пароль был отправлен на почту в момент создания формы раскрытия. Если вы
      не получили пароль для входа на страницу, напишите на почту
      <a
        target="_blank"
        class="text-decoration-none login-form__link"
        href="mailto:kto@skoltech.ru"
        >kto@skoltech.ru</a
      >
    </VTypography>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import PasswordInput from "@/components/ui/PasswordInput";
import ActionButton from "@/components/buttons/ActionButton";

export default {
  name: "LoginForm",
  components: {
    VTypography,
    ActionButton,
    PasswordInput,
  },
  data() {
    return {
      password: "",
    };
  },
  emits: {
    submit: () => true,
  },
  methods: {
    onSubmit() {
      this.$emit("submit", this.password);
    },
  },
};
</script>

