<template>
  <div
    class="d-flex user-select-none position-relative align-items-center justify-content-center v-language"
    :class="rootClasses"
    @mouseenter="showMenu = true"
    @mouseleave="showMenu = false"
  >
    <VTypography font-style="text-2" class="text-uppercase">{{
      modelValue
    }}</VTypography>

    <div
      v-if="showMenu"
      class="d-flex position-absolute flex-column v-language__menu"
    >
      <VButton
        :key="language"
        v-for="language in list"
        @click="clickItem(language)"
        class="d-flex align-items-center justify-content-center v-language__menu-item"
      >
        <VTypography font-style="text-2" class="text-uppercase">{{
          language
        }}</VTypography>
      </VButton>
    </div>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import VButton from "@/components/buttons/VButton";

export default {
  name: "VLanguage",
  components: { VButton, VTypography },
  data() {
    return {
      showMenu: false,
    };
  },
  props: {
    modelValue: String,
    languages: {
      type: Array,
      required: true,
      default: () => [],
    },
  },
  methods: {
    clickItem(language) {
      this.showMenu = false;
      this.$emit("update:modelValue", language);
    },
  },
  computed: {
    rootClasses() {
      return {
        "v-language--active": this.showMenu,
      };
    },
    list() {
      return this.languages.filter((x) => x !== this.modelValue);
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.v-language {
  width: 32px;
  height: 32px;
  cursor: pointer;
  box-sizing: content-box;
  border: 2px solid $gray1;
}

.v-language--active {
  color: $white;
  background-color: $gray1;
}

.v-language__menu {
  top: 32px;
  border: 2px solid $gray1;
}

.v-language__menu-item {
  width: 32px;
  height: 32px;
}
</style>
