<template>
  <div class="d-flex">
    <div class="d-flex" :class="containerClasses">
      <VTypography
        :font-style="fontStyle"
        :font-weight="fontWeight"
        class="typography-with-info__label"
      >
        <slot></slot>
      </VTypography>
      <VPopover class="mx-1" v-if="hasPopover" :position="popoverPosition">
        <template v-slot:content="slotProps">
          <InfoIcon v-if="!hasIcon" :active="slotProps.active" :size="16" />

          <slot v-if="hasIcon" name="icon" v-bind="slotProps"></slot>
        </template>

        <template v-slot:popover>
          <slot name="popover"></slot>
        </template>
      </VPopover>
    </div>
  </div>
</template>

<script>
import VPopover from "@/components/popover/Popover";
import InfoIcon from "@/components/icons/InfoIcon";
import VTypography from "@/components/ui/Typography";

export default {
  name: "TypographyWithInfo",
  components: { VTypography, InfoIcon, VPopover },
  props: {
    fontStyle: String,
    fontWeight: String,
    popoverPosition: String,
    position: {
      type: String,
      default: "right",
      validate(value) {
        return ["left", "right"].includes(value);
      },
    },
  },
  computed: {
    hasIcon() {
      return !!this.$slots.icon;
    },
    hasPopover() {
      return !!this.$slots.popover;
    },
    containerClasses() {
      return {
        "align-items-center flex-row-reverse": this.position === "left",
      };
    },
  },
};
</script>
